html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3 {
  margin: 0, 0, 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3.2rem;
}

.header {
  padding: 5rem 0 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 5rem;
  margin-bottom: 5rem;
}
@media (min-width: 1024px) {
  .header {
    flex-direction: row;
    justify-content: space-between;
  }
}
.header__logo {
  width: 30rem;
}
.header .navegacion {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  width: 100%;
}
@media (min-width: 1024px) {
  .header .navegacion {
    flex-direction: row;
    width: auto;
  }
}
.header .navegacion__element {
  color: #000;
  font-weight: 700;
  font-size: 2rem;
  text-transform: capitalize;
}
.header .navegacion__element--red {
  background-color: #e22331;
  font-size: 2rem;
  padding: 1.2rem 3rem;
  border-radius: 1rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #FFF;
  text-decoration: none;
  transition: background-color 300ms ease;
  border: none;
}
.header .navegacion__element--red:hover {
  cursor: pointer;
  background-color: rgb(185.5421686747, 24.4578313253, 36.265060241);
}
@media (min-width: 768px) {
  .header .navegacion__element--red {
    width: auto;
  }
}

@media (min-width: 768px) {
  .hero {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.hero__formulario {
  background-color: #E8505B;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  padding: 5rem;
}
.hero__imagen {
  display: flex;
  justify-content: center;
  margin-top: 5rem;
}
.hero__imagen img {
  width: 30rem;
}
@media (min-width: 1024px) {
  .hero__imagen img {
    width: 100%;
  }
}
.hero__title {
  margin: 0 0 5rem;
  color: #FFF;
  font-size: 3.6rem;
}

.formulario__label {
  color: #FFF;
  text-transform: uppercase;
  margin: 1rem 0;
  display: block;
}
.formulario__input {
  padding: 1.5rem;
  width: 100%;
  border: none;
}
.formulario__btn {
  background-color: #455A64;
  font-size: 2rem;
  padding: 1.2rem 3rem;
  border-radius: 1rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #FFF;
  text-decoration: none;
  transition: background-color 300ms ease;
  border: none;
}
.formulario__btn:hover {
  cursor: pointer;
  background-color: rgb(48.1775147929, 62.8402366864, 69.8224852071);
}
.formulario__btn {
  margin-top: 2rem;
}

.pasos__img {
  margin: 5rem 0 -1px;
}

.como-funciona {
  background-color: #E8505B;
}
.como-funciona__title, .como-funciona__p {
  margin: 0;
  color: #FFF;
  text-align: center;
}
.como-funciona__p {
  font-weight: 700;
  font-size: 1.8rem;
}

.card-container {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .card-container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 5rem;
  }
}
.card-container .card {
  transition: transform 300ms;
}
.card-container .card:hover {
  transform: rotate(-4deg) scale(1.2);
}

.testimoniales {
  margin-bottom: 5rem;
}
.testimoniales__title {
  text-align: center;
  margin: 5rem 0;
}
@media (min-width: 768px) {
  .testimoniales__grid {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 2rem;
  }
}
.testimoniales blockquote {
  margin: 2rem 0 0;
  font-size: 2rem;
  line-height: 1.5;
  position: relative;
  padding-left: 3rem;
}
.testimoniales blockquote:before {
  content: '"';
  font-size: 10rem;
  display: inline;
  position: absolute;
  top: -4rem;
  left: 0rem;
  padding: 0 0 1rem;
  opacity: 0.2;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande", "Lucida Sans", Arial, sans-serif;
}

.testimoniales__card {
  background-color: #455A64;
  color: #FFF;
  padding: 2rem;
  margin-bottom: 2rem;
  border-radius: 1rem;
}
.testimoniales__card:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .testimoniales__card {
    margin-bottom: 0;
  }
  .testimoniales__card:nth-of-type(1) {
    grid-column: 1/4;
  }
  .testimoniales__card:nth-of-type(3) {
    grid-column: 5/6;
    grid-row: 1/3;
  }
  .testimoniales__card:nth-of-type(4) {
    grid-column: 1/3;
  }
  .testimoniales__card:nth-of-type(5) {
    grid-column: 3/5;
  }
}
.testimoniales__card .usuario {
  display: flex;
  gap: 2rem;
  align-items: center;
}
.testimoniales__card .usuario .usuario__foto {
  flex-basis: 10rem;
  border-radius: 50%;
  overflow: hidden;
}
.testimoniales__card .usuario .usuario__foto img {
  height: 100%;
}
.testimoniales__card .usuario__name {
  margin: 0.5rem 0;
  color: #FFF;
  font-weight: 700;
}

.favoritos {
  background-color: #ECEEEF;
  padding: 5rem 0;
}
.favoritos__title {
  margin: 0 0 5rem;
  text-transform: capitalize;
  color: #455A64;
}
@media (min-width: 768px) {
  .favoritos .ensaladas-contenedor {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 4rem;
  }
}

.ensaladas__card {
  padding: 4rem;
  background-color: #FFF;
  margin-bottom: 2rem;
  border-radius: 1rem;
  box-shadow: 0 0 2.6rem -0.8rem #99a4a9;
}
@media (min-width: 768px) {
  .ensaladas__card {
    display: flex;
    align-items: center;
    gap: 4rem;
    margin-bottom: 0;
  }
}
.ensaladas__card .ensaladas__img {
  max-width: 20rem;
  margin: 0 auto 2rem;
}
@media (min-width: 768px) {
  .ensaladas__card .ensaladas__img {
    flex: 1;
  }
}
.ensaladas__card .ensaladas__info {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 768px) {
  .ensaladas__card .ensaladas__info {
    align-items: flex-start;
    flex: 3;
  }
}
.ensaladas__card .ensaladas__info .ensaladas__title {
  color: #E8505B;
  font-size: 2.4rem;
  margin: 1rem 0;
  text-transform: capitalize;
}
.ensaladas__card .ensaladas__info p {
  font-size: 1.6rem;
  line-height: 1.7;
  color: #455A64;
  text-align: center;
  margin: 0.5rem 0;
}
@media (min-width: 768px) {
  .ensaladas__card .ensaladas__info p {
    text-align: left;
  }
}

.empleo {
  padding: 0 0 5rem;
}
@media (min-width: 768px) {
  .empleo__contenedor {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
    align-items: center;
  }
}
@media (min-width: 1024px) {
  .empleo__contenedor {
    align-items: stretch;
  }
}
.empleo .empleo__info {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: flex-start;
}
.empleo__title, .empleo__p {
  color: #455A64;
}
.empleo__title {
  margin: 5rem 0;
}
.empleo__p {
  line-height: 1.5;
  margin-bottom: 4rem;
}
@media (min-width: 768px) {
  .empleo__p {
    margin: 2rem 0;
  }
}
.empleo__p:last-of-type {
  margin-bottom: 2rem;
}
.empleo__btn {
  background-color: #E8505B;
  font-size: 2rem;
  padding: 1.2rem 3rem;
  border-radius: 1rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #FFF;
  text-decoration: none;
  transition: background-color 300ms ease;
  border: none;
}
.empleo__btn:hover {
  cursor: pointer;
  background-color: rgb(226.0757575758, 34.9242424242, 48.7575757576);
}
.empleo__btn {
  text-transform: capitalize;
}
@media (min-width: 768px) {
  .empleo__btn {
    width: auto;
  }
}

.text-center {
  text-align: center;
}

.bg-primario {
  background-color: #E8505B;
}

.bg-blanco {
  background-color: #FFF;
}

.margin-negativo {
  margin-top: -10rem;
}

.padding-bottom-0 {
  padding-bottom: 0;
}